@import "variables.scss";

.ring-past {
  max-width: 2150px;
  margin-left: -250px;
  padding-left: 250px;
  margin-top: 150px;
  background: url(../img/ring-past-back1.png) no-repeat;
  @media (max-width: 1430px) {
    margin-left: -250px;
    padding-left: 250px;
  }

  @media (max-width: 1330px) {
    margin-left: -300px;
    padding-left: 300px;
  }

  &-container {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: 150px;
    padding-bottom: 150px;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
      overflow: hidden;
    }
  }

  &__text {
    font-size: 20px;
    color: #ffffff;
  }
}